	module.exports = {"data":{"markdownRemark":{"html":"<p>In order for hackers to hack on private programs, they must receive invitations. Hackers are able to receive invitations if they meet the following criteria:</p>\n<ul>\n<li>Established reputation</li>\n<li>Non-negative signal</li>\n<li>Clear record with zero code of conduct violations</li>\n</ul>\n<p>HackerOne manages your invitations by:</p>\n<ul>\n<li>Daily checking to see if your program has met your report volume target in the last 30 days</li>\n<li>Inviting hackers for your program if you’re not reaching your report volume target</li>\n</ul>\n<h3>How Invitations Work</h3>\n<p>The number of invitations HackerOne sends is based on your report volume on a rolling 30 day basis. These are the use cases as to how HackerOne sends invitations:</p>\n<table>\n<thead>\n<tr>\n<th>Case</th>\n<th>Action</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>You're far from meeting your report volume.</td>\n<td>HackerOne will gradually increase the number of invitations so you can meet your report volume.</td>\n</tr>\n<tr>\n<td>You're receiving more reports while still being under your report volume.</td>\n<td>HackerOne will send out fewer invitations.</td>\n</tr>\n<tr>\n<td>Your report volume is met.</td>\n<td>Invitations will pause until the volume drops below the target in the 30 day window.</td>\n</tr>\n<tr>\n<td>Your report volume is set to 0.</td>\n<td>No invitations will be sent.</td>\n</tr>\n</tbody>\n</table>\n<blockquote>\n<p><i>Note: HackerOne will be sure to prevent you from being overwhelmed with reports.</i></p>\n</blockquote>\n<h3>Setting Invitations</h3>\n<p>To set your invitations:</p>\n<ol>\n<li>Go to <strong>Settings > Program > Hacker Management > Invitations</strong>.</li>\n<li>\n<p>Enter the number of reports you’d like to receive every 30 days in the <strong>Report volume</strong> field. HackerOne recommends starting out by setting a target of receiving 5 valid reports every 30 days.</p>\n<ul>\n<li>The report volume is measured by the amount of valid reports submitted in the past 30 days. Valid reports include reports that have been marked by you as <i>Triaged</i> or <i>Resolved</i>. Reports that are left in <i>New</i> or marked to any other state won't count toward this goal. Learn more about <a href=\"/programs/report-states.html\">report states</a>.</li>\n</ul>\n</li>\n<li>Click <strong>Save</strong>.</li>\n</ol>\n<h3>Manually Inviting Hackers</h3>\n<p>Sometimes you might want to issue your own invitations to hackers. If you need to issue your own invitations to particular hackers as an exception:</p>\n<ol>\n<li>Go to <strong>Settings > Program > Hacker Management > Invitations</strong>.</li>\n<li>\n<p>In the <strong>Invite a hacker by</strong> field, you can choose to invite a hacker by selecting either:</p>\n<ul>\n<li>Email</li>\n<li>Username</li>\n</ul>\n</li>\n</ol>\n<p>HackerOne doesn’t recommend inviting unknown hackers. If a hacker has reached out to request an invitation to your private program, contact HackerOne as this is a clear violation of Disclosure Guidelines.</p>\n<h3>Managing Invitations</h3>\n<p>You can keep track of your invitations and which hackers have been invited to your program.</p>\n<ul>\n<li>The <strong>Invited Hackers</strong> section lists all of the hackers that have accepted invitations. You can select Remove next to the hacker you want to remove from your program.</li>\n<li>In the <strong>Pending Invitations</strong> section, you can cancel pending invitations.</li>\n</ul>","frontmatter":{"path":"/programs/invitations.html","id":"programs/invitations","title":"Invitations","description":null}}},"pathContext":{}}

